import React from 'react'
import './RegForm.css'

class RegForm extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            name: "",
            surname: "",
            login: "",
            password: "",
            image: null
        }
    }

    render() {
        const {name, surname, login, password} = this.state;
        return(
            <div className="regList">
                <h2>Авторизация</h2>
                <input className="textInput" type="text" placeholder="Имя"></input>
                <input className="textInput" type="text" placeholder="Фамилия"></input>
                <input className="textInput" type="text" placeholder="Login"></input>
                <input className="textInput" type="text" placeholder="Пароль"></input>
                <input type="image"></input>
                <button>Отправить</button>
            </div>
        );
    }
}

export default RegForm;